.user-work-back {
    display: flex;
    align-items: center;
    cursor: pointer;
}
.attendance-section .user-work p {
    display: flex;
    font-size: 12px;
    color: #777777;
    padding-left: 8px;
    padding-bottom: 0 !important;
    margin: 0 !important
}
.attendance-section .user-work-btns  .input {
    padding: 10px 8px;
    background: none;
}
.attendances {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.4rem;
    font-family: 'Montserrat', sans-serif;
}
.attendance {
    border-radius: 20px;
    box-shadow: 0 8px 10px 6px rgba(131,134,163,.12);
    background-color: white;
    padding: 20px;
    display: flex;
}
.attendance h1 {
    font-size: 18px;
}
.attendance-desc {
    padding-left: 20px;
}
.attendance-btns button {
    outline: none;
    border: none;
    font-weight: 500;
}
.attendance-btns button:last-child {
    background-color: rgba(255, 97, 119, .05);
    margin-left: 10px;
}
.radio-toolbar input[type="radio"] {
    opacity: 0;
    position: fixed;
    width: 0;
}
.present {
    display: inline-block;
    padding: 12px 23px;
    font-size: 12px;
    background-color: rgba(3, 206, 164, .05);
    border-radius: 5px;
    font-weight: 500;
    cursor: pointer;
    user-select: none;
}
.absent {
    display: inline-block;
    padding: 12px 23px;
    font-size: 12px;
    background-color: rgba(255, 97, 119, .05);
    border-radius: 5px;
    font-weight: 500;
    cursor: pointer;
    margin-left: 15px;
    user-select: none;
}

.radio-toolbar input[type="radio"]:checked + .present {
    background-color: #03CEA4;
    color: white;
}

.radio-toolbar input[type="radio"]:checked + .absent {
    background-color: #FF6177;
    color: white;
}
