/*Navbar*/
.wrapper nav {
    width: 120px;
    height: 100vh;
    background-color: #0063B2;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 30px;
}
nav .logo img{
    width: 80px;
    height: 80px;
    text-align: center;
    border-radius: 100%;
}
.nav-links {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
    overflow-y: auto;
    width: 100%;
}

.nav-links {
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scrollbar-width: none;  /* Firefox */
}
.nav-links::-webkit-scrollbar {
    display: none;  /* Safari and Chrome */
}

.nav-links li {
    list-style: none;
    padding: 8px 0px;
    text-align: center;
}
.nav-links li a {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
    text-align: center;
    color: white;
    font-size: 14px;
    padding: 18px 13px;
    border-radius: 5px;
    font-family: 'SF Pro Display', sans-serif;
    font-weight: 300;
    width: 90px;
    height: 90px;
    justify-content: center;
}
.nav-links li a img {
    width: 36px;
    height: 36px;
    padding-bottom: 7px;
}
.nav-links li a.active {
    background-color: #005090;
}
