/*Attendance List*/
.attendance-table {
    padding: 5px 0px 20px 0px;
    box-shadow: 0 8px 10px 6px rgba(131,134,163,.12);
    font-family: 'Montserrat', sans-serif;
    border-radius: 20px;
}
table {
    width: 100%;
}
th {
    font-size: 16px;
    padding: 12px;
    text-align: center;
}
th.users {text-align: start}
th.last {
    display: flex;
    align-items: center;
}
th img {
    padding-right: 6px;
}
th span {
    color: #8896A4;
}
td {
    min-width: 120px;
    border-top: 1px solid #CDCDCD;
    text-align: center;
    padding: 10px;
}
td.users {
    text-align: start;
    font-weight: 500;
}
td img{
    padding-right: 10px;
}
td .present, td .absent {
    padding: 10px 16px;
}
td .absent {
    background-color: rgba(3, 206, 164, .1);
    margin-left: 0px;
    cursor: default;
}
td .present {
    background-color: rgba(255, 97, 119, .1);
    margin-left: 10px;
    cursor: default;
}
.active-absent {
    color: white;
    background-color: #FF6177 !important;
}
.active-present {
    color: white;
    background-color: #03CEA4 !important;
}
td button {
    outline: none;
    border: none;
    background-color: #0063B2;
    padding: 10px 20px;
    font-size: 12px;
    color: white;
    border-radius: 5px;
    cursor: pointer;
}
.pages {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin: 20px 0px
}
.pages span {
    cursor: pointer;
}
.pages .material-icons {
    font-size: 28px;
    color: #8896A4;
}
.pages .page {
    margin: 0px 10px;
}
.pages .dots {
    margin: 0px 3px;
}
.pages .active-page {
    background-color: #0063B2;
    padding: 4px 9px;
    font-size: 13px;
    color: white;
    border-radius: 3px;
}
