.instructor-form {
    width: 70%;
    margin: auto;
    box-shadow: 0 8px 10px 6px rgba(131,134,163,.12);
    font-family: 'Montserrat', sans-serif;
    border-radius: 5px;
    padding: 25px 35px;
    font-family: 'Montserrat', sans-serif;
    background-color: white;
    margin-top: 20px;
}
.login-form {
    width: 40%;
}
.instructor-form .input-fields {
    grid-gap: 1.7rem;
}
label span {
    margin-left: 8px;
    color: #8896A4;
    font-weight: 400;
}
.form-showcase {
    text-align: center;
}
.form-showcase h1 {
    font-weight: bold;
    font-size: 30px;
    padding: 25px 0px 10px 0px;
}
.form-showcase p {
    font-size: 16px;
    font-weight: 500;
}
.signup-type-btns {
    display: flex;
    border-bottom: 1px solid #CDCDCD;
}
.signup-type-btns button {
    font-size: 20px;
    padding: 10px;
    margin-top: 5px;
    outline: none;
    background: none;
    border: none;
    cursor: pointer;
    width: 100%;
}
.personal-details form , .school-details form {
    padding-top: 15px;
}
.instructor-form button.active {
    font-weight: 500;
    border-bottom: 3px solid #0063B2;
}
form .home-address {
    font-size: 16px;
    font-weight: 500;
    padding: 12px 0px 3px 0px;
}
form button {
    outline: none;
    border: none;
    background-color: #0063B2;
    color: white;
    padding: 14px 0px;
    font-size: 14px;
    border-radius: 5px;
    margin-top: 7px;
}
form .login {
    text-align: center;
    font-weight: 500;
    font-size: 16px;
    padding-top: 7px;
}
form .login span {
    color: #0063B2;
    font-weight: bold;
    cursor: pointer;
}

